/* General layout */
.app {
  font-family: sans-serif;
  padding: 1rem;
  text-align: center;
}

/* Row with draw/discard piles */
.piles {
  display: flex;
  justify-content: center;
  gap: 1rem;       /* same as Tailwind gap-4 */
  margin-bottom: 1rem;
}

/* Pile styling */
.pile {
  width: 80px;
  height: 120px;
  border: 2px solid #333;
  border-radius: 8px;
  background: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.pile.disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.pile-label {
  font-size: 0.8rem;
  margin-top: 0.25rem;
}

/* Player grid wrapper */
.player-board {
  margin-bottom: 2rem;
}

/* 3x4 grid of cards */
.grid {
  display: grid;
  grid-template-columns: repeat(4, 80px);
  grid-gap: 8px;
  justify-content: center;
  margin-top: 1rem;
}

/* Single card cell */
.cell {
  width: 80px;
  height: 120px;
  border: 2px solid #666;
  border-radius: 6px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #fafafa;
  font-size: 1.2rem;
  cursor: pointer;
}

.cell.face-down {
  background: #444;
  color: transparent;
}

.cell.removed {
  visibility: hidden;
}
